@import url(//db.onlinewebfonts.com/c/b158925a1d762313849f2482a415369d?family=Eina03-SemiBold);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-menu {
  height: 305px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: white;
  font-family: Eina03-SemiBold;
}

.ul-main {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: scroll;
  white-space: nowrap;
  list-style: none;
  text-decoration: none;
  font-size: 16px;
}

.ul-main li {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Eina03-SemiBold;
  line-height: 18.75px;
  position: relative;
  left: 520px;
  top: -3.5px;
  margin-left: 22px;
}

.fixedbutton {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Eina03-SemiBold;
  font-size: 16px;
  width: 84px;
  min-width: 84px;
  height: 30px;
  margin-left: 23px;
  margin-bottom: 24px;
  margin-right: 20px;
  border: none;
  background-color: #dcdfec;
  border-radius: 4px;
  cursor: pointer;
}

.smaller-button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  width: 31px;
  min-width: 31px;
  height: 30px;
  margin-left: 23px;
  margin-bottom: 24px;
  margin-right: 20px;
  border: none;
  background-color: #dcdfec;
  border-radius: 4px;
  cursor: pointer;
}

.control-text-button {
  margin-top: 2.5px;
}

.remove-text {
  display: none;
}

.img-space {
  margin: 5px;
}

.remove-notification {
  display: none;
}

.notification {
  display: block;
  position: absolute;
  top: 248px;
  left: 45px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 14px;
  height: 14px;
  color: #FFFFFF;
  background-color: #FC6C44;
  border-radius: 50%;
  font-size: 10px;
  line-height: 12px;
}

@media only screen and (max-width: 320px) {
  .ul-main li {
    left: 537.5px;
  }
}
